.subscribe-block {
    margin-top: 96px;
    background: linear-gradient(180deg, #f3f2f2 0%, #dcdbdb 100%);
    display: flex;
    justify-content: center;

    .small-block-wrapper {
        position: relative;
  
        .small-block {
            display: flex;
            flex-direction: column;
            align-items: center;
            background: #fff;
            border-radius: 4px;
            margin: 60px 0;
            padding: 80px 96px 96px;
  
            .title {
                font-weight: 600;
                line-height: 13px;
                letter-spacing: 0.06em;
                color: #a0a0a0;
            }
  
            .subtitle {
                margin-top: 16px;
                font-weight: 600;
                font-size: 26px;
                line-height: 36px;
                letter-spacing: 0.06em;
                max-width: 284px;
                text-align: center;

                .percent {
                    color: #e91e63;
                }
            }
  
        .input {
            margin-top: 34px;
            width: 100%;
            background: #f8f8f8;
            border: none;
            min-height: 64px;
            display: flex;
            align-items: center;
            text-align: center;
            font-weight: 500;
            font-size: 16px;
            line-height: 18px;
            color: #9c9c9c;
            outline: 0;
  
                &::placeholder {
                    font-weight: 500;
                    font-size: 16px;
                    line-height: 18px;
                    color: #9c9c9c;
                }
                &:focus::placeholder {
                    color: transparent;
                }
        }
  
        .button {
            margin-top: 24px;
            font-weight: 500;
            font-size: 16px;
            line-height: 18px;
            letter-spacing: 0.06em;
            padding: 22px 0;
            width: 100%;
            border: none;
            background: #121212;
            color: #ffffff;
            cursor: pointer;
  
            &:hover {
                background: #414141;
                transition: all 0.3s;
            }
        }
      }
  
        .woman {
            position: absolute;
            top: 35px;
            left: -259px;
        }
  
        .men {
            position: absolute;
            bottom: 11px;
            right: -205px;
        }
    }
}

@media screen and (max-width: 960px) {
    .woman, .man {
        display: none;
    }
}
  
@media screen and (max-width: 790px) {
    .subscribe-block {
        margin-top: 48px;
    }
}
  
@media screen and (max-width: 580px) {
    .subscribe-block {
        .small-block-wrapper {
            .small-block {
                padding: 24px;
            }
        }
    }
}

@media screen and (max-width: 390px) {
    .subscribe-block {
        .small-block-wrapper {
            .small-block {
                .subtitle {
                    font-size: 20px;
                    line-height: 26px;
                }
  
                .input {
                    min-width: 240px;
                }
    
                .button {
                    max-width: 240px;
                    display: flex;
                    justify-content: center;
                }
            }
        }
    }
}